.itemInfo{
    font-size: 20px;
    line-height: 36px;
    margin: 50px auto 0;
    width: 75%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__quantity,
    &__price{
    text-align: right;
    }
    &__price--strike{
        text-decoration: line-through;
    }
}